/* Assistive Tech –––––––––––––––––––––––––––––––––––––––––––––––––– */
/**
 * Text meant only for screen readers. It's hidden due to the clip property, but
 * visible in the page source. If you take a look back in the template files at
 * the mark-up for our main menu and other navigation areas, you'll notice that
 * we have headers such as "Menu" and "Site Navigation" wrapped in an
 * <code>h1</code> tag with a class of "assistive-text". Screen readers will
 * read this text aloud, making it easier for users to locate structural
 * sections in your theme.
 */
.assistive-text,
.screen-reader-text,
.sr-only {
  position: absolute !important;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  word-wrap: normal !important;
  border: 0;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  // Many screen reader and browser combinations announce broken words as they would appear visually.  word-break: normal;
  &:active,
  &:focus,
  &:hover {
    font-size: 0.875rem;
    font-weight: bold;
    line-height: normal;
    z-index: 100000;
    // Above WP toolbar.
    top: 5px;
    left: 5px;
    display: block;
    clip: auto !important;
    width: auto;
    height: auto;
    padding: 15px 23px 14px;
    text-decoration: none;
    color: rgba(0, 0, 0, .6);
    border-radius: 3px;
    background-color: #f1f1f1;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    -webkit-clip-path: none;
    clip-path: none;
  }
}

.skip-link:focus {
  font-size: 0.875rem;
  font-weight: 700;
  line-height: normal;
  z-index: 100000;
  top: 5px;
  left: 5px;
  display: block;
  clip: auto !important;
  width: auto;
  height: auto;
  padding: 15px 23px 14px;
  text-decoration: none;
  color: #21759b;
  border-radius: 3px;
  background-color: #f1f1f1;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, .6);
  -webkit-clip-path: none;
  clip-path: none;
}

.assistive-text {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

#primary[tabindex='-1']:focus {
  outline: 0;
}
@media (prefers-reduced-motion) {
  * {
    transition-delay: 0s !important;
    transition-duration: 0s !important;
  }
}
